import Reflux from 'reflux';
import Actions from '../actions/actions';
import User from '../data/user';

var UserStore = Reflux.createStore({
    listenables: [Actions],
    user: [],
    getUser: function() {
    	this.user = User;
    	let store = this;
    	window.setTimeout(function(){
    		store.trigger(store.user);
    	},2000)
    }
}); 

export default UserStore;



