import React from 'react';
import Reflux from 'reflux';
import _ from 'underscore';

import Actions from '../actions/actions';
import MachineStore from '../stores/machineStore';

var Machine = React.createClass({

	buildKeypad(){
		let p = this.props.machine;
		let disabledMarker = (typeof p.key_1 !== "undefined" && typeof p.key_2 !== "undefined") ? true : false;
		let keys = [];
		_.chain(s.keypad)
			.each((key, index) => {
				keys.push(
					<button disabled={disabledMarker} onClick={Actions.setKey1().bind(this, key.id)}>
						{key.n}
					</button>
				)	
			})

		return 	<div>
			<div className="keys">{keys}</div>	
			<button className="btn-pos">BUY</button>
			<button className="btn-neg">CANCEL</button>
			<button className="btn-neu">CASH OUT</button>
		</div>
	},

	buildMachineContent(){
		let p = this.props;
		let items = [];
		_.chain(p.machine.items)
			.each(item => {
				let keycode = _.findWhere(p.machine.keypad, {id: item.key_1}).n +
					_.findWhere(p.machine.keypad, {id: item.key_2}).n;
				let cur = _.findWhere(p.cur.currencies, {id: p.cur.currency});

				items.push(
					<div className="item">
						<span>{item.dsp_name}</span>
						<span>{keycode} - {item.price*cur.multiplier}{cur.dsp_name}</span>
						<span>{item.qtty}</span>
					</div>
				)	
			})

		return 	items;
	},

	render() {
		let p = this.props;
		let cur = _.findWhere(p.cur.currencies, {id: p.cur.currency});
		return (
			<div className="machine">
				<div className="machine-balance">
					<div>{p.machine.balance*cur.multiplier}{cur.dsp_name}</div>
				</div>
				<div class="machine-display">
					<div>{p.machine.message}</div>
				</div>
				<div className="machine-keypad">
					{this.buildKeypad()}
				</div>
				<div className="machine-content">
					{this.buildMachineContent()}
				</div>
			</div>
		);
	}
})

export default Machine;



